body{
  font-family: Arial, Helvetica, sans-serif;
}
h1
h2,
h3{
  margin: 0.5rem 0;
}
h1{
  font-size: 1.5rem;
}
h1{
  font-size: 1.2rem;
}
h3{
  font-size: 1rem;
}
a{
  text-decoration: none;
}
.row{
  display: flex;
  justify-content: space-between;
}
.col-1{
  flex: 1;
}
.col-2{
  flex:2;
}
.block {
  background-color: #66c3df;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.row.center{
  align-items: center;
}
img.small{
  max-height: 8rem;
}
button{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.5rem;
  border: 0.1rem #404040 solid;
  background-color: aquamarine;
  width: 100%;
  cursor: pointer;
}
button.add{
  background-color: darkcyan;
  width: 1.5rem;
}
button.remove{
  background-color: red;
  width: 1.5rem;
}
button.badge{
  background-color: red;
  border:none;
  width: 1.5rem;
  color: #ffffff;
}
.text-right{
  text-align: right;
}